Form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
}

Form input {
  font-size: 30px;
  border: 2px solid gray;
  border-radius: 10px;
  box-shadow: 3px 0px 2px rgba(0, 0, 0, 0.192);
  margin-bottom: 10px;
}

Form label {
  font-size: 30px;
  text-align: left;
  padding: 5px;
}

Form button {
  font-size: 30px;
  text-align: left;
  margin-top: 20px;
}

p {
  color: red;
}
